.section {
  padding-top: 50px;
  padding-bottom: 45px;

  &_gray-1 {
    background-color: $color-athens-gray1;
  }

  &_project {
    padding-top: 50px;
    padding-bottom: 30px;
    background-color: $color-athens-gray2;
  }

  &__advantages {
    padding-top: 30px;
    padding-bottom: 30px;
    @include media-breakpoint-down(md) {
      padding-top: 10px;
      padding-bottom: 10px;
    }
  }

  &__service {
    position: relative;
    padding-bottom: 0;
  }

  &_bg {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
  }

  &__consult {
    position: relative;

    &_bg {
      display: none;
      @include media-breakpoint-down(md) {
        display: block;
        position: absolute;
        bottom: 0;
        height: 50%;
        width: 100%;
        left: 0;
        right: 0;
        background-color: $color-dark-blue;
        z-index: -1;
      }
    }
  }

  &__header {
    text-transform: uppercase;
    color: $color-darkgrey;
    margin-bottom: 30px;
    @include heading-3-bold;

    &_red {
      text-transform: uppercase;
      color: $color-red;
    }

    &_white {
      color: #fff;
    }
  }

  &__subheader {
    @include heading-3;
  }

  &__header-wrapper {
    margin-bottom: 30px;
  }

  &__catalog {
    padding-top: 0;
    padding-bottom: 165px;
  }

  &__row {
    @include media-breakpoint-up(md) {
      .col-md-7 {
        width: 61.54%;
      }

      .col-md-5 {
        width: 38.46%;
      }
    }
  }



  &__card,
  &__projects {
    padding-top: 20px;
    padding-bottom: 100px;

    h1 {
      margin-bottom: 35px;
    }
  }

  &__card {
     .col-lg-9 {
       @include media-breakpoint-up(md) {
         padding-right: 8px;
       }
     }
     .col-lg-3 {
        @include media-breakpoint-up(md) {
         padding-left: 7px;
       }
     }

     h1 {
       @media print {
           text-align: center;
      }
     }

  }

  &__projects {
    .card__col-left {
      margin-top: 5px;
    }
    .feedback {
      flex-direction: column;

      &__rating-block {
        padding-left: 0;
      }
    }
  }


  &__payment {
    padding-top: 25px;
    padding-bottom: 70px;

    h1 {
      margin-bottom: 15px;
    }
  }

  &__installation {
    padding-top: 25px;
    padding-bottom: 70px;
  }

  &__brand {
    padding-top: 25px;
    padding-bottom: 70px;

    h1 {
      margin-bottom: 20px;
    }
  }

  &__delivery {
    padding-top: 25px;
    padding-bottom: 70px;

    h1 {
      margin-bottom: 25px;
    }
  }

  &__article {
    padding-top: 25px;
    padding-bottom: 90px;

    h1 {
      margin-bottom: 25px;
    }
  }

  &__select {
    padding-bottom: 120px;
    padding-top: 15px;
    h1 {
      margin-bottom: 25px;
    }

    .select-panel {
      @include media-breakpoint-between(md, lg) {
        margin-right: -1px;
      }
    }
  }

  &__compare {
    padding-bottom: 135px;
    padding-top: 30px;
    h1 {
      margin-bottom: 25px;
    }
  }

  &__contacts {
    padding-bottom: 135px;
    padding-top: 30px;

    h1 {
      margin-bottom: 25px;
    }
  }
}
.section_sm {
  padding-top: 30px;
  padding-bottom: 30px;

  &__header {
    text-transform: uppercase;
    padding-bottom: 10px;

    @include heading-4;
  }
}
